import React, { PureComponent, PropTypes } from 'react'
import 'active.css/less/components/alert.less'


const { bool, string, object, func, any } = PropTypes

const propTypes = {
  className: string,
  style: object,
  children: any,
  noClose: bool,
  onClose: func,
}

const defaultProps = {
  noClose: false,
}

export default class Infobar extends PureComponent {
  render() {
    const { className, style, children, noClose, onClose } = this.props

    return (
      <div
        className={`infobar ${className || ''}`}
        style={style}
      >
        {children}
        {noClose || <button type='button' className='close' onClick={onClose}>×</button>}
      </div>
    )
  }
}

Infobar.propTypes = propTypes
Infobar.defaultProps = defaultProps
Infobar.displayName = 'AAUIInfobar'



