import { PureComponent, PropTypes, Children } from 'react'

import L10n from './L10n'
import defaultValidationMessages from '../form/i18n/ValidationMsg.json'
import commonMessages from '../i18n/common/CommonMsg.json'
import { aauiL10nPropTypes } from './types'

if (process.env.NODE_ENV === 'test') {
  require.ensure = (deps, cb) => cb(require)
  require.context = () => () => {}
}

const { string, object, node } = PropTypes
const propTypes = {
  locale: string,
  messages: object,
  children: node,
}
const defaultProps = {
  locale: 'en_US',
}
const childContextTypes = { ...aauiL10nPropTypes }
/* eslint-disable global-require, import/no-dynamic-require */
const lazyLoadI18N = (locale, cb) =>
  require.ensure([], require => cb(require.context('../../i18n', false, /\.json$/)))

export default class L10nProvider extends PureComponent {
  constructor(props, context) {
    super(props, context)

    this.l10n = new L10n(props, context)
  }

  getChildContext() {
    return {
      aauiL10n: this.l10n,
    }
  }

  componentDidMount() {
    const { locale } = this.props

    if (!locale) {
      return
    }

    lazyLoadI18N(locale, req => {
      this.l10n.messages = {
        ...this.l10n.messages,
        ...defaultValidationMessages[locale],
        ...commonMessages[locale],
        ...req(`./${locale}.json`),
      }
    })
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.locale !== nextProps.locale ||
      this.props.messages !== nextProps.messages) {
      this.l10n.locale = nextProps.locale

      const locale = this.l10n.locale
      lazyLoadI18N(locale, req => {
        this.l10n.messages = {
          ...this.l10n.messages,
          ...defaultValidationMessages[locale],
          ...commonMessages[locale],
          ...req(`./${locale}.json`),
          ...nextProps.messages,
        }
      })
    }
  }

  render() {
    return Children.only(this.props.children)
  }
}

L10nProvider.propTypes = propTypes
L10nProvider.defaultProps = defaultProps
L10nProvider.childContextTypes = childContextTypes



