import React, { PropTypes } from 'react'

import { cls } from './shared/utils'
import { tabsAPIShape } from './shared/types'


const { string, object, any } = PropTypes

const propTypes = {
  className: string,
  style: object,
  name: string,
  children: any,
  aauiTabsAPI: tabsAPIShape,
}

const TabsContainer = ({ className, style, name, children, aauiTabsAPI: { getSelected } }) => (
  <div
    className={cls`${name !== getSelected() ? 'u-hidden' : ''} ${className || ''}`}
    style={style}
  >
    {children}
  </div>
)

TabsContainer.displayName = 'AAUITabsContainer'
TabsContainer.propTypes = propTypes

export default TabsContainer



