import React, { PureComponent, PropTypes } from 'react'
import 'active.css/less/components/pagination.less'

import { cls } from './shared/utils'

function pageData(type, theme, total, current, around, url, roundText, startText, endText) {
  function pageLink(page) {
    if (typeof url === 'function') {
      return url(page)
    }

    let pageLinkVar = typeof url === 'string' ? url : ''
    if (pageLinkVar.indexOf('?') > -1) {
      pageLinkVar += '&page'
    } else {
      pageLinkVar += '?page'
    }
    return `${pageLinkVar}=${page}`
  }

  const start = 1
  const end = total
  const aroundStart = Math.max(current - around, start + 1)
  const aroundEnd = Math.min(current + around, end - 1)
  const data = []

  switch (type) {
    case 'pager':
      if (current > start) {
        data.push({ text: startText, href: pageLink(current - 1) })
      }
      if (current < end) {
        data.push({ text: endText, href: pageLink(current + 1) })
      }
      break
    case 'pagination':
      if (current > start) {
        data.push({ text: startText, href: pageLink(current - 1) })
      }
      /* eslint prefer-template: 0 */
      data.push({ text: '' + start, href: pageLink(start) })
      if (aroundStart - start > 1) {
        data.push({ text: roundText })
      }
      for (let i = aroundStart; i < current; i += 1) {
        data.push({ text: '' + i, href: pageLink(i) })
      }
      for (let i = (current !== start ? current : current + 1); i <= aroundEnd; i += 1) {
        data.push({ text: '' + i, href: pageLink(i) })
      }
      if (end - aroundEnd > 1) {
        data.push({ text: roundText })
      }
      data.push({ text: '' + end, href: pageLink(end) })
      if (current < end) {
        data.push({ text: endText, href: pageLink(current + 1) })
      }
      break
    default:
      break
  }

  return data
}

const { number, string, object, oneOfType } = PropTypes

const propTypes = {
  className: string,
  type: string,
  theme: string,
  total: number,
  current: number,
  around: number,
  endText: oneOfType([object, string]),
  linkElement: string,
  startText: oneOfType([object, string]),
  roundText: oneOfType([object, string]),
  url: string,
}

const defaultProps = {
  type: 'pagination',
  url: '',
  around: 3,
  linkElement: 'a',
  roundText: '...',
  startText: <span className="icon-uniF053" />,
  endText: <span className="icon-uniF054" />,
}

export default class Pagination extends PureComponent {
  render = () => {
    const {
      type, theme, total, current, around, url,
      roundText, startText, endText,
      linkElement: A, className, ...attrs
    } = this.props

    return (
      <ul
        className={cls`${type}
                       ${type === 'pagination' && theme ? 'pagination--' + theme : ''}
                       ${className || ''}`}
        {...attrs}
      >
        {pageData(type, theme, total, current, around, url, roundText, startText, endText)
          .map(({ text, href }, i) => {
            if (href && +text !== current) {
              return (
                <li key={i} className={cls`${+text === current ? 'active' : ''}`}>
                  <A key={i} href={href}>
                    {text}
                  </A>
                </li>
              )
            } else { /* eslint no-else-return: 0 */
              return (<li
                key={i}
                className={cls`${+text === current ? 'active' : ''}`}
              >
                <span key={i}>
                  {text}
                </span>
              </li>)
            }
          })}
      </ul>
    )
  }
}

Pagination.displayName = 'AAUIPagination'
Pagination.propTypes = propTypes
Pagination.defaultProps = defaultProps



