import React, { PureComponent, PropTypes } from 'react'
import 'active.css/less/components/input.less'
import 'active.css/less/components/input-group.less'

import { cls } from './shared/utils'


const { bool, string, func, object, any } = PropTypes

const propTypes = {
  value: string,
  defaultValue: string,
  type: string.isRequired,
  size: string,
  preIcon: string,
  preText: string,
  postIcon: string,
  postText: string,
  errored: bool,
  disabled: bool,
  className: string,
  style: object,
  onChange: func,
  children: any,
}

const defaultProps = {
  type: 'text',
}

export default class Input extends PureComponent {
  constructor(props) {
    super(props)

    const { value = '', defaultValue = '' } = props

    this.state = {
      value: value || defaultValue,
    }
  }

  componentDidMount() {
    Object.defineProperties(this, {
      value: {
        get() {
          return this.input.value
        },
        set(v) {
          if (this.props.value === undefined) {
            this.input.value = v
          }
        },
      },
    })
  }

  // Data displayed by a controlled component is received through props passed down
  // from it's parent/container component.
  // Sync props -> state
  componentWillReceiveProps(nextProps) {
    if (nextProps.value !== this.props.value) {
      this.setState({
        value: nextProps.value,
      })
    }
  }

  setWrappedComponentInstance = input => {
    this.input = input
  }

  // Controlled components have functions to govern the data going into them
  // on every onChange event, rather than grabbing the data only once,
  // e.g. when a user clicks a submit button.
  // This 'governed' data is then saved to state
  handleChange = e => {
    // If you want to access the event properties in an asynchronous way,
    // you should call `event.persist()`` on the event,
    // which will remove the synthetic event from the pool
    // and allow references to the event to be retained by user code.
    e.persist()

    this.setState({
      value: e.target.value,
    }, () => {
      if (this.props.onChange) {
        this.props.onChange(e)
      }
    })
  }

  render() {
    const {
      type, size, preIcon, preText, postIcon, postText, errored, disabled,
      className, style, ...rest
    } = this.props

    return (
      <div
        className={cls`input-group
        ${size ? `input-group--${size}` : ''}
        ${errored ? 'input-group--error' : ''}
        ${className || ''}`}
        style={style}
      >
        {preIcon || preText ?
          <span
            className='input-group__item'
          >
            {preIcon && <i className={cls`icon ${preIcon}`} />}
            {preText}
          </span> : undefined
        }
        <input
          {...rest}
          type={type}
          disabled={disabled}
          className='input input-group__field'
          ref={this.setWrappedComponentInstance}
          value={this.state.value}
          onChange={this.handleChange}
        />
        {postIcon || postText ?
          <span
            className='input-group__item'
          >
            {postIcon && <i className={cls`icon ${postIcon}`} />}
            {postText}
          </span> : undefined
        }
      </div>
    )
  }
}

Input.displayName = 'AAUIInput'
Input.propTypes = propTypes
Input.defaultProps = defaultProps



