import React, { PureComponent, PropTypes } from 'react'
import 'active.css/less/components/alert.less'

import { cls } from './shared/utils'


const { bool, string, func, object, any, oneOf } = PropTypes

const icons = {
  success: 'success',
  warning: 'warn',
  danger: 'close-circle',
  info: 'info',
}

const propTypes = {
  type: oneOf(['success', 'warning', 'danger', 'info']).isRequired,
  className: string,
  style: object,
  noClose: bool,
  onClose: func,
  children: any,
}

const defaultProps = {
  type: 'info',
  noClose: false,
}

export default class Alert extends PureComponent {
  render() {
    const { className, style, type, noClose, onClose, children } = this.props

    return (
      <div
        className={cls`alert
        alert-${type}
        ${className || ''}
        ${noClose ? undefined : 'alert-dismissable'}`}
        style={style}
      >
        <span className={`icon-${icons[type]}`} />
        {children}
        {noClose
          ? undefined
          : <button type='button' className='close' onClick={onClose}>×</button>
        }
      </div>
    )
  }
}

Alert.displayName = 'AAUIAlert'
Alert.propTypes = propTypes
Alert.defaultProps = defaultProps




