import React, { PureComponent, PropTypes } from 'react'

import NestedForm from './NestedForm'
import Form from '../Form'
import TextInput from './TextInput'
import Combobox from './Combobox'
import PostalCodeInput, { POSTAL_CODE_FIELD_KEY } from './PostalCodeInput'
import getContext from '../shared/getContext'
import { aauiL10nPropTypes } from '../shared/types'

const { string, object, func, node, oneOfType } = PropTypes
const propTypes = {
  countriesConfig: object.isRequired,
  fields: object,
  country: string,
  fieldLayout: oneOfType([node, func]),
  ...aauiL10nPropTypes,
}
const defaultProps = {
  country: 'US',
  fields: {},
  fieldLayout: Form.HField,
}
const FIELD_PREFIX = 'addressForm.addressField'

class AddressEditor extends PureComponent {
  constructor(props, context) {
    super(props, context)

    this.l10n = props.aauiL10n || context.aauiL10n
    this.state = this.getInitState()
  }

  componentDidMount() {
    this.unsubscribe = this.l10n.subscribe(() => this.forceUpdate())
  }

  componentWillUnmount() {
    if (this.unsubscribe) {
      this.unsubscribe()
    }

    this.unsubscribe = null
  }

  getInitState() {
    const { fields } = this.props
    let country = fields.country

    if (!country) {
      country = this.props.country
    }
    return {
      country,
      fields: this.reduceFields(country),
    }
  }

  getAvailCountries() {
    const { countriesConfig } = this.props

    return Object.keys(countriesConfig).map(
      country => ({
        text: this.l10n.formatMessage(`country.displayName.${country}`),
        value: country,
      }),
    )
  }

  // Split field value into multiple parts for `line2.2`, `city.2`, etc.
  reduceFields = country => {
    const { fields, countriesConfig } = this.props
    const countryConfig = countriesConfig[country]

    if (!countryConfig || !countryConfig.addressForm) {
      return {}
    }

    const countryAddressFields = countryConfig.addressForm.addressFields

    return Object.keys(countryAddressFields).reduce((r, k) => {
      const countryAddressField = countryAddressFields[k]
      const { addressPart, position } = countryAddressField
      const value = fields[addressPart]

      if (!value) {
        return r
      }

      const valueArr = fields[addressPart].split('\n')

      // It means it has multiple parts for one address part
      if (position > 1) {
        return {
          ...r,
          [`${addressPart}.${position}`]: valueArr[position - 1] || '',
        }
      }

      return {
        ...r,
        [addressPart]: valueArr[0],
      }
    }, { country })
  }

  reduceCountryAddressFields = (country = '') => {
    const { countriesConfig } = this.props
    const countryConfig = countriesConfig[country]

    if (!countryConfig || !countryConfig.addressForm) {
      return []
    }

    const code = countryConfig.iso3166Code
    const countryAddressFields = countryConfig.addressForm.addressFields || []

    return Object.keys(countryAddressFields).reduce((r, k) => {
      const countryAddressField = countryAddressFields[k]
      const { required, addressPart, position, options = [] } = countryAddressField

      // Find the label key
      let label = `${FIELD_PREFIX}.${code}.${addressPart}`
      if (position > 1) {
        label = `${label}.${position}`
      }
      if (!this.l10n.formatMessage(label)) {
        label = `${FIELD_PREFIX}.${addressPart}`
      }

      const component = TextInput
      const data = options.map(
        option => ({
          text: this.l10n.formatMessage(`${addressPart}.displayName.${code}.${option.fieldOptionValue}`),
          value: option.fieldOptionValue,
        }),
      )
      let reducedAddressField = {
        name: position > 1 ? `${addressPart}.${position}` : addressPart,
        label,
        component,
        required,
        data,
      }

      if (options && options.length !== 0) {
        reducedAddressField = {
          ...reducedAddressField,
          component: Combobox,
          maxHeight: '320px',
        }
      }

      if (addressPart === POSTAL_CODE_FIELD_KEY) {
        reducedAddressField = {
          ...reducedAddressField,
          countryConfig,
          component: PostalCodeInput,
        }
      }

      return [...r, reducedAddressField]
    }, [])
  }

  handlePreChange = values => {
    const { countriesConfig } = this.props
    const { country } = this.state
    const valueKeys = Object.keys(values)
    const countryConfig = countriesConfig[country]
    const countryAddressFields = countryConfig.addressForm.addressFields

    return Object.keys(countryAddressFields).reduce((r, k) => {
      const countryAddressField = countryAddressFields[k]
      const { addressPart, position } = countryAddressField
      const addressPartPart = `${addressPart}.${position}`
      const addressPartPartValue = values[addressPartPart]

      // If found `line2.2`, `city.2`, `stateProvince.2`, `line2.3` or `city.3`
      if (position > 1 && addressPartPartValue && valueKeys.indexOf(addressPartPart)) {
        return { ...r, [addressPart]: `${r[addressPart]}\n${addressPartPartValue}` }
      }

      return { ...r, [addressPart]: values[addressPart] }
    }, { country })
  }

  handleCountryChange = ({ value: country }) => {
    this.setState({ country })
  }

  render() {
    const { fieldLayout: FieldLayout, ...rest } = this.props
    const { country, fields } = this.state
    const countries = this.getAvailCountries()
    const reducedCountryAddressFields = this.reduceCountryAddressFields(country)

    return (
      <NestedForm {...rest} fields={fields} preChange={this.handlePreChange}>
        <Form>
          <FieldLayout
            maxHeight='320px'
            name='country'
            label={`${FIELD_PREFIX}.country`}
            component={Combobox}
            data={countries}
            defaultValue={country}
            onChange={this.handleCountryChange}
          />
          {
            reducedCountryAddressFields.map(f => (
              <FieldLayout
                key={f.name}
                name={f.name}
                {...f}
              />
            ))
          }
        </Form>
      </NestedForm>
    )
  }
}

AddressEditor.propTypes = propTypes
AddressEditor.defaultProps = defaultProps

export default getContext({ ...aauiL10nPropTypes })(AddressEditor)



