import React, { PureComponent, PropTypes } from 'react'
import 'active.css/less/components/label.less'

const { string, object, oneOf, node } = PropTypes
const propTypes = {
  className: string,
  style: object,
  type: oneOf(['success', 'warning', 'danger', 'info']).isRequired,
  children: node,
}
const defaultProps = {
  type: 'info',
}

export default class Label extends PureComponent {
  render() {
    const { className, style, type, children, ...rest } = this.props

    return (
      <span
        {...rest}
        className={`label label-${type} ${className || ''}`}
        style={style}
      >
        {children}
      </span>
    )
  }
}

Label.displayName = 'AAUILabel'
Label.propTypes = propTypes
Label.defaultProps = defaultProps



