import React, { PureComponent, PropTypes } from 'react'

import * as da from './shared/data-access'
import { cls } from './shared/utils'
import Tag from './Tag'

import './TagEditor.less'

let lastFocusedElement = null

function trackLastFocusedElement() {
  lastFocusedElement = document.activeElement
}

function stopPropagation(e) {
  e.stopPropagation()
}

const propTypes = {
  className: PropTypes.string,
  data: PropTypes.oneOfType([PropTypes.array, PropTypes.object]).isRequired,
  onChange: PropTypes.func,
  errored: PropTypes.bool,
}

const defaultProps = {
  data: [],
  errored: false,
}

export default class TagEditor extends PureComponent {
  componentDidUpdate(prevProps) {
    if (da.count(this.props.data) > da.count(prevProps.data)) {
      const lastTag = this.tag
      if (lastTag) lastTag.toInput()
    }
  }

  addTag = () => {
    if (lastFocusedElement && lastFocusedElement.tagName === 'INPUT') return
    const d = this.props.data
    const data = da.push(Array.from(d), { text: 'your tag name', isNew: true })
    const onChange = this.props.onChange
    if (onChange) onChange(data)
  }

  removeTag = ind => () => {
    const d = this.props.data
    const data = da.del(Array.from(d), ind)
    const onChange = this.props.onChange
    if (onChange) onChange(data)
  }

  editTag = ind => (text, isKeyFinished) => {
    const d = this.props.data
    let data = d
    if (text === '') {
      data = da.del(Array.from(d), ind)
    } else {
      data = da.set(
        Array.from(d),
        ind,
        { text },
      )

      if (isKeyFinished && ind === da.count(d) - 1) {
        data = da.push(Array.from(data), { text: 'your tag name', isNew: true })
      }
    }

    const onChange = this.props.onChange
    if (onChange) onChange(data)
  }

  removeEmptyTagIfNew = ind => () => {
    const d = this.props.data
    let data = d

    if (da.getIn(d, ind, 'isNew')) {
      data = da.del(Array.from(d), ind)
      const onChange = this.props.onChange
      if (onChange) onChange(data)
    }
  }

  render() {
    /* eslint-disable no-unused-vars */
    const { className, data, onChange, errored, ...rest } = this.props

    return (
      <div
        className={cls`aaui-tageditor
          ${errored ? 'aaui-tageditor-errored' : ''}
          ${className || ''}`}
        {...rest}
        onMouseDown={trackLastFocusedElement}
        onClick={this.addTag}
      >
        {data.map((tag, i) => (
          <Tag
            key={i}
            ref={tagRef => { this.tag = tagRef }}
            value={da.get(tag, 'text')}
            isNew={!!da.get(tag, 'isNew')}
            editMode
            onChange={this.editTag(i)}
            onCancel={this.removeEmptyTagIfNew(i)}
            onClose={this.removeTag(i)}
            onClick={stopPropagation}
          />
        ))
        }
      </div>
    )
  }
}

TagEditor.displayName = 'AAUITagEditor'
TagEditor.propTypes = propTypes
TagEditor.defaultProps = defaultProps



