import React, { PureComponent, PropTypes } from 'react'
import 'active.css/less/components/sidebar.less'

import { cls } from './shared/utils'
import * as da from './shared/data-access'


const { bool, string, number, object, array, element, oneOfType } = PropTypes

const propTypes = {
  className: string,
  style: object,
  data: array,
  links: bool,
  steps: bool,
  active: number,
  past: number,
  priority: string,
  title: string,
  linkElement: oneOfType([string, element]),
}

const defaultProps = {
  priority: '5',
  steps: false,
  linkElement: 'a',
}

export default class Sidebar extends PureComponent {
  render() {
    const {
      className, style, data = [], steps, active, past,
      priority, title, links, linkElement: A,
    } = this.props

    const HeaderElement = `h${priority}`

    return (
      <aside
        className={cls`sidebar
          ${links ? 'sidebar--links' : ''}
          ${className || ''}`}
        style={style}
      >
        <HeaderElement>{title}</HeaderElement>
        <ul>
          {
            data.map((item, i) => {
              const stepNum = steps ? `${i + 1}.` : undefined

              return (
                <li
                  key={i}
                >
                  <A
                    className={cls`${i === active ? 'active' : ''}
                      ${i === past ? 'past' : ''}`}
                    href={da.get(item, 'href')}
                  >
                    {stepNum} {da.get(item, 'text')}
                  </A>
                </li>
              )
            })
          }
        </ul>
      </aside>
    )
  }
}

Sidebar.displayName = 'AAUISidebar'
Sidebar.propTypes = propTypes
Sidebar.defaultProps = defaultProps



