import { PureComponent, createElement } from 'react'

import Store from './store'
import getDisplayName from '../shared/getDisplayName'
import { identity, reduceReducers } from '../shared/utils'
import { ValidationResult, ruleRunner } from './validation'
import reducer, { setValue, setError } from './module'
import { FormFieldPropTypes, FormStorePropTypes } from './types'
import { aauiL10nPropTypes } from '../shared/types'

const aauiFormStore = Object.keys(FormStorePropTypes)[0]
const FormFieldConfig = {
  /* eslint-disable no-unused-vars */
  validator: ({ l10n }) => validationResult => validationResult,
  parser: identity,
  formatter: identity,
}
const connectForm = (formFieldConfig = {}) => WrappedComponent => {
  const connectDisplayName = `Connect(${getDisplayName(WrappedComponent)})`
  const propTypes = {
    ...FormFieldPropTypes,
    ...aauiL10nPropTypes,
  }

  class Connect extends PureComponent {
    constructor(props, context) {
      super(props, context)

      this.store = props[aauiFormStore] || context[aauiFormStore] || new Store()
      this.l10n = props.aauiL10n || context.aauiL10n
    }

    componentWillMount() {
      if (!this.unregister) {
        this.unregister = this.store.injectReducer(this.props.name, reducer)
      }

      if (!this.unregisterValidator && this.store.tryInjectValidator) {
        this.unregisterValidator = this.store.tryInjectValidator(this.props.name, this.validate)
      }
    }

    componentWillUnmount() {
      if (this.unregister) {
        this.unregister(this.props.name)
      }

      this.unregister = null

      if (this.unregisterValidator) {
        this.unregisterValidator(this.props.name)
      }

      this.unregisterValidator = null
    }

    onValidate = value => {
      const { errMsg } = this.validate(value)

      this.setError(errMsg)
    }

    setValue = value => {
      this.store.dispatch(setValue({
        name: this.props.name,
        value,
      }))
    }

    setError = value => {
      this.store.dispatch(setError({
        name: this.props.name,
        value,
      }))
    }

    getWrappedComponentInstance() {
      return this.wrappedComponentInstance
    }

    setWrappedComponentInstance = ref => {
      this.wrappedComponentInstance = ref
    }

    getApi = () => ({
      l10n: this.l10n,
      onValidate: this.onValidate,
      setValue: this.setValue,
      setError: this.setError,
    })

    validate = value => {
      const { name, required, validator, parser, rules = '', ...rest } = this.props
      const validators = []

      validators.push(ruleRunner)

      // Here is for your custom validation function
      if (validator && typeof validator === 'function') {
        validators.push(validator)
      }

      const chain = validators.map(
        validateFunc => validateFunc({
          l10n: this.l10n,
          rules: required ? `required|${rules}` : rules,
          ...rest,
        }),
      )
      const { errMsg } = reduceReducers(...chain)(
        new ValidationResult(name, parser(value), null),
      )

      return {
        name,
        value,
        errMsg,
      }
    }

    render() {
      /* eslint-disable no-unused-vars */
      const {
        required,
        validator,
        errMsg,
        formatter,
        parser,
        value,
        rules,
        ...rest
      } = this.props

      this.renderedElement = createElement(WrappedComponent, {
        ref: this.setWrappedComponentInstance,
        api: this.getApi(),
        value: errMsg ? value : formatter(value),
        ...rest,
      })

      return this.renderedElement
    }
  }

  Connect.displayName = connectDisplayName
  Connect.WrappedComponent = WrappedComponent
  Connect.propTypes = propTypes
  Connect.contextTypes = {
    ...FormStorePropTypes,
    ...aauiL10nPropTypes,
  }
  Connect.defaultProps = { ...FormFieldConfig, ...formFieldConfig }

  return Connect
}

export default connectForm



