import React, { PureComponent, PropTypes } from 'react'
import 'active.css/less/components/form.less'
import 'active.css/less/components/grid.less'

import FormError from './form/FormError'
import HFormFieldLayout from './form/HFormFieldLayout'
import VFormFieldLayout from './form/VFormFieldLayout'
import createField, { isRegisteredField } from './form/createField'
import Store from './form/store'
import { noop, identity, shallowEqual, filterProps } from './shared/utils'
import { FormPropTypes, FormFieldPropTypes, FormStorePropTypes } from './form/types'
import { reduceFields } from './form/utils'

const aauiFormStore = Object.keys(FormStorePropTypes)[0]
const FormDefaultConfig = {
  preSubmit: noop,
  shouldSubmit: () => true,
  onSubmit: noop,
  postSubmit: noop,
  onFail: noop,
  preChange: identity,
  onChange: noop,
  fields: {},
}

export default class Form extends PureComponent {
  constructor(props, context) {
    super(props, context)

    // Sync the `this.state` and `this.store.currentState`
    this.state = this.getInitState()
    this.parentStore = props[aauiFormStore] || context[aauiFormStore]
    this.store = new Store({
      ...props,
      initialState: this.state.fields,
    })
    // Keep the child store reference from the parent store
    if (this.parentStore) {
      this.parentStore.childStoreList = [...this.parentStore.childStoreList, this.store]
    }
  }

  getChildContext() {
    return {
      [aauiFormStore]: this.store,
    }
  }

  componentDidMount() {
    this.unsubscribe = this.store.subscribe(this.handleChange)
  }

  componentWillUnmount() {
    if (this.unsubscribe) {
      this.unsubscribe()
      this.unsubscribe = null
    }
  }

  getInitState() {
    /* eslint-disable max-len */
    // {fieldName:{defaultValue:"first name",validator:validator,rules:"",parser:a=>a,formatter:a=>a},email:{defaultValue:"defalut@defalut.com"}};
    const { fields } = this.props
    const reducedFields = Object.keys(fields).filter(k => fields[k]).reduce((r, k) => {
      const field = fields[k]
      let value

      // If `field` is string then it's value by default
      if (typeof field === 'string') {
        value = field
      } else {
        value = field.value || field.defaultValue
      }

      return {
        ...r,
        [k]: {
          ...filterProps(field, Object.keys(FormFieldPropTypes)),
          value,
        },
      }
    }, {})

    return { fields: reducedFields, isValid: true }
  }

  handleSubmit = e => {
    /* eslint-disable */
    e && e.preventDefault && e.preventDefault()

    this.store.tryInvokeValidators()

    const { preSubmit, shouldSubmit, onSubmit, postSubmit, onFail } = this.props
    const { values, errors, isValid } = reduceFields(this.store.getState())

    if (!isValid) {
      return onFail(errors)
    }

    if (shouldSubmit(values, preSubmit(values))) {
      onSubmit(values)
    }

    return postSubmit(values)
  }

  handleChange = () => {
    const fields = this.store.getState()
    const { values, errors, isValid } = reduceFields(fields)
    const { values: currentValues, errors: currentErrors } = reduceFields(this.state.fields)
    const { preChange, onChange } = this.props

    if (!shallowEqual(currentValues, values) || !shallowEqual(currentErrors, errors)) {
      // trigger 'onChange' here on purpose
      onChange({ values: preChange(values), errors })

      this.setState({ fields, isValid })
    }
  }

  // Here maybe it's better to have react's `context` but it's fine now
  traverseChildren(children, fields) {
    return React.Children.map(children, child => {
      // If `child` is not Element then just return it
      if (!React.isValidElement(child)) {
        return child
      }

      let childProps = {}

      if (isRegisteredField(child.type)) {
        childProps = {
          ...childProps,
          ...fields[child.props.name],
        }
      }

      childProps.children = this.traverseChildren(child.props.children, fields)

      return React.cloneElement(child, childProps)
    })
  }

  render() {
    const { children, className = 'form--horizontal' } = this.props
    const { fields } = this.state
    const formChildren = this.traverseChildren(children, fields)

    // If we have the `this.parentStore` then it means we're the nested form
    if (this.parentStore) {
      return (
        <div>{formChildren}</div>
      )
    }

    // Todo: Update the error message with `Form.Error`
    return (
      <form
        className={className}
        onSubmit={this.handleSubmit}
      >
        {formChildren}
      </form>
    )
  }
}

Form.propTypes = { ...FormPropTypes }
Form.contextTypes = { [aauiFormStore]: PropTypes.instanceOf(Store) }
Form.childContextTypes = { ...FormStorePropTypes }
Form.defaultProps = { ...FormDefaultConfig }
Form.displayName = 'AAUIForm'
// Form Fields
Form.Field = createField()
Form.HField = createField(HFormFieldLayout)
Form.VField = createField(VFormFieldLayout)
Form.Error = FormError

/**
 * Provide utility function and lifecycle methods for `Form`
 */
export const createForm = (formConfig = {}) => WrappedComponent => {
  class Connect extends PureComponent {
    render() {
      return (
        <WrappedComponent {...this.props} />
      )
    }
  }

  Connect.defaultProps = {
    ...FormDefaultConfig,
    ...formConfig,
  }

  return Connect
}



