import React, { PureComponent, PropTypes } from 'react'

import connectForm from './connectForm'
import RadioGroup from '../RadioGroup'
import { FormFieldAPIPropTypes } from './types'

const { string, node } = PropTypes
const propTypes = {
  ...FormFieldAPIPropTypes,
  selected: string,
  children: node,
}

class FormRadioGroup extends PureComponent {
  constructor(props) {
    super()

    this.state = {
      selected: props.value || '',
    }
  }

  handleChange = e => {
    const { api: { setValue }, onChange } = this.props
    const value = e.target.value

    this.setState({ selected: value }, () => {
      setValue(value)

      if (typeof onChange === 'function') {
        onChange(e)
      }
    })
  }

  render() {
    /* eslint-disable no-unused-vars*/
    const { api: { setValue }, fields, children, ...rest } = this.props
    const { selected } = this.state

    return (
      <RadioGroup
        {...rest}
        selected={selected}
        onChange={this.handleChange}
      >
        {children}
      </RadioGroup>
    )
  }
}

FormRadioGroup.displayName = 'FormRadioGroup'
FormRadioGroup.propTypes = propTypes

export default connectForm()(FormRadioGroup)



