import React, { PropTypes } from 'react'


const { string, object, any } = PropTypes

const propTypes = {
  className: string,
  style: object,
  children: any,
}

const TabsHeader = ({ className, style, children }) => (
  <ul
    className={`nav-tabs ${className || ''}`}
    style={style}
  >
    {children}
  </ul>
)

TabsHeader.displayName = 'AAUITabsHeader'
TabsHeader.propTypes = propTypes

export default TabsHeader



