import React, { PureComponent, PropTypes } from 'react'
import 'active.css/less/components/tag.less'

import { cls } from './shared/utils'
import KEY_CODES from './shared/keyCodes'


const { string, func, bool } = PropTypes
const propTypes = {
  className: string,
  value: string,
  onChange: func,
  onCancel: func,
  onClose: func,
  editMode: bool,
  isNew: bool,
}
const defaultProps = {
  editMode: false,
}

export default class Tag extends PureComponent {
  constructor(props) {
    super(props)

    this.state = {
      isInput: false,
      isTabEnter: false,
    }
  }

  handleKeyDown = e => {
    const target = e.target

    switch (e.keyCode) {
      case KEY_CODES.ESCAPE:
        e.preventDefault()
        this.setState({
          isInputCancel: true,
          isTabEnter: false,
        }, () => {
          target.blur()
        })
        break
      case KEY_CODES.TAB:
      case KEY_CODES.ENTER:
        e.preventDefault()

        this.setState({
          isTabEnter: true,
        }, () => {
          if (target) {
            target.blur()
          }
        })
        break
      default:
        break
    }
  }

  handleBlur = e => {
    const value = e.target.value
    this.setState({
      isInput: false,
    }, () => {
      if (!this.state.isInputCancel) {
        const onChange = this.props.onChange
        if (onChange) onChange(value, this.state.isTabEnter)
      } else {
        const onCancel = this.props.onCancel
        if (onCancel) onCancel()
      }
    })
  }

  toInput = e => {
    e && e.stopPropagation && e.stopPropagation()

    this.setState({
      isInput: true,
      isInputCancel: false,
    }, () => {
      const input = this.input

      if (input) {
        input.focus()
        input.select()
      }
    })
  }

  render() {
    /* eslint-disable no-unused-vars */
    const { onCancel, className, editMode, onClose, onChange, value, isNew, ...rest } = this.props
    const { isInput } = this.state

    if (!editMode) {
      return <span className='badge'>{value}</span>
    }

    return (
      <span
        className={cls`tag-input
        ${className || ''}`}
        {...rest}
      >
        {isInput ? (
          <input
            className='input'
            ref={input => { this.input = input }}
            placeholder={isNew ? value : undefined}
            defaultValue={isNew ? undefined : value}
            onKeyDown={this.handleKeyDown}
            onBlur={this.handleBlur}
          />
        ) : (
          <span className='badge' onClick={onClose}>
            <span onClick={this.toInput}>{value}</span>
          </span>
        )}
      </span>
    )
  }
}

Tag.displayName = 'AAUITag'
Tag.propTypes = propTypes
Tag.defaultProps = defaultProps



