import React, { PureComponent, PropTypes } from 'react'
import 'active.css/less/components/radio.less'

import KEY_CODES from './shared/keyCodes'

const { bool, string, object, func, any } = PropTypes
const propTypes = {
  size: string,
  children: any,
  className: string,
  style: object,
  disabled: bool,
  checked: bool,
  defaultChecked: bool,
  onChange: func,
}
const defaultProps = {
  size: '',
}

export default class Radio extends PureComponent {
  constructor(props) {
    super(props)

    const { defaultChecked = false, checked = false } = props

    this.state = {
      checked: defaultChecked || checked,
    }
  }

  componentDidMount() {
    Object.defineProperties(this, {
      checked: {
        get() {
          return this.state.checked
        },
        set(v) {
          this.setState({
            checked: !!v,
          }, () => {
            if (this.props.onChange) {
              this.props.onChange({
                target: this.input,
              })
            }
          })
        },
      },
    })
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.checked !== this.props.checked) {
      this.setState({
        checked: nextProps.checked,
      })
    }
  }

  setWrappedComponentInstance = input => {
    this.input = input
  }

  handleChange = e => {
    e.persist()

    this.setState({
      checked: e.target.checked,
    }, () => {
      if (this.props.onChange) {
        this.props.onChange(e)
      }
    })
  }

  handleKeyDown = e => {
    e.persist()

    // Prevent scrolling when pressing any scroll key (e.g. down arrow, spacebar) when on a widget
    switch (e.keyCode) {
      case KEY_CODES.SPACE: // SPACEBAR
        e.preventDefault()

        this.setState({
          checked: !this.state.checked,
        }, () => {
          if (this.props.onChange) {
            // We must change the `target` property
            // in order to feed the `onChange` callback when triggering on the container
            this.props.onChange({
              ...e,
              target: this.input,
            })
          }
        })
        break
      case KEY_CODES.UPARROW: // UPARROW
      case KEY_CODES.DOWNARROW: // DOWNARROW
        e.preventDefault()
        break
      default:
        break
    }
  }

  render() {
    /* eslint-disable no-unused-vars */
    const { size, children, className, style, disabled, defaultChecked, ...rest } = this.props

    /* eslint-disable jsx-a11y/label-has-for */
    return (
      <label
        className={`radio ${size ? `radio--${size}` : ''} ${className || ''}`}
        style={style}
        onKeyDown={this.handleKeyDown}
      >
        <input
          ref={this.setWrappedComponentInstance}
          disabled={disabled}
          type='radio'
          aria-hidden
          {...rest}
          checked={this.state.checked}
          onChange={this.handleChange}
        />
        <span
          role='radio'
          className='radio__text'
          tabIndex={disabled ? -1 : 0}
          aria-disabled={disabled}
          aria-checked={this.state.checked}
        >
          {children}
        </span>
      </label>
    )
  }
}

Radio.displayName = 'AAUIRadio'
Radio.propTypes = propTypes
Radio.defaultProps = defaultProps



