import React, { PureComponent } from 'react'

import L10n from './L10n'
import getContext from './getContext'
import { aauiL10nPropTypes } from './types'

const propTypes = {
  ...aauiL10nPropTypes,
}

class L10nMessage extends PureComponent {
  constructor(props, context) {
    super(props, context)

    this.l10n = props.aauiL10n || context.aauiL10n || new L10n()
    this.unsubscribe = null
  }

  componentDidMount() {
    // Todo: maybe we should use `setState` instead of `forceUpdate` here
    this.unsubscribe = this.l10n.subscribe(() => this.forceUpdate())
  }

  componentWillUnmount() {
    if (this.unsubscribe) {
      this.unsubscribe()
    }

    this.unsubscribe = null
  }

  render() {
    const { id, values } = this.props

    return (
      <span>
        {this.l10n.formatMessage(id, values, id)}
      </span>
    )
  }
}

L10nMessage.propTypes = propTypes

export default getContext({
  ...aauiL10nPropTypes,
})(L10nMessage)



