import React, { PureComponent, PropTypes } from 'react'
import 'active.css/less/components/modal.less'

import { cls } from './shared/utils'

const { string, bool, object, func, array, element, oneOfType } = PropTypes

const propTypes = {
  className: string,
  onClose: func,
  title: string,
  style: object,
  shown: bool,
  children: oneOfType([array, object, func, element]),
}

const defaultProps = {
  shown: false,
}


export default class Modal extends PureComponent {

  componentDidMount = () => {
    if (this.props.shown) {
      this.fixBody()
    }
  }

  componentDidUpdate = ({ shown }) => {
    if (shown !== this.props.shown) {
      if (this.props.shown) {
        this.fixBody()
      } else {
        this.unfixBody()
      }
    }
  }

  fixBody = () => {
    const bodyStyles = document.body.style
    const scrollbarWidth = window.innerWidth - document.documentElement.clientWidth

    this.bodyOverflowX = bodyStyles.overflowX
    this.bodyOverflowY = bodyStyles.overflowY
    this.bodyPaddingRight = bodyStyles.paddingRight

    bodyStyles.overflowX = bodyStyles.overflowY = 'hidden'
    if (scrollbarWidth > 0) {
      const paddingRight = getComputedStyle(document.body).paddingRight
      this.root.style.paddingRight = bodyStyles.paddingRight = `${Number(paddingRight) + Number(scrollbarWidth)}px`
    }
  }

  unfixBody = () => {
    const bodyStyles = document.body.style
    bodyStyles.overflowX = this.bodyOverflowX
    bodyStyles.overflowY = this.bodyOverflowY
    bodyStyles.paddingRight = this.bodyPaddingRight
    this.root.style.paddingRight = ''
  }

  render = () => {
    const { className, style, title, onClose, children } = this.props

    return (
      <div className='modal-wrap'>
        <div ref={ref => { this.root = ref }} className={cls`modal ${this.props.shown ? 'is-open' : ''} ${className || ''}`} style={style}>
          <section ref={ref => { this.box = ref }} className='modal-box'>
            <header className='modal-header'>
              <h3 className='modal-title'>{title}</h3>
              <span onClick={onClose} className='icon-close modal-close' />
            </header>
            {children}
          </section>
        </div>
        <div className='modal-mask' />
      </div>
    )
  }
}

Modal.displayName = 'AAUIModal'
Modal.propTypes = propTypes
Modal.defaultProps = defaultProps



