import React, { PureComponent, PropTypes } from 'react'
import 'active.css/less/components/tooltip.less'

import { cls } from './shared/utils'

const propTypes = {
  className: PropTypes.string,
  style: PropTypes.object,
  theme: PropTypes.string,
  label: PropTypes.string,
  direction: PropTypes.string,
  children: PropTypes.any,
}

const defaultProps = {
  direction: 'n',
}

export default class Tooltip extends PureComponent {
  render() {
    const { className, style, theme, direction, children, label } = this.props

    return (
      <span
        className={cls`tooltips
                       ${theme ? `t-${theme}` : ''}
                       tooltips--${direction}
                       ${className || ''}`}
        style={style}
      >
        {label}
        <span className='tooltips__content'>{children}</span>
      </span>
    )
  }
}

Tooltip.displayName = 'AAUITooltip'
Tooltip.propTypes = propTypes
Tooltip.defaultProps = defaultProps



