import React, { PureComponent, PropTypes } from 'react'
import 'active.css/less/components/tab.less'

import getContext from './shared/getContext'
import TabsHeader from './TabsHeader'
import TabsTitle from './TabsTitle'
import TabsContainer from './TabsContainer'
import { tabsAPIShape } from './shared/types'


const { string, object, func, any } = PropTypes

const propTypes = {
  className: string,
  style: object,
  selected: string,
  defaultSelected: string,
  onChange: func,
  children: any,
}

const childContextTypes = {
  aauiTabsAPI: tabsAPIShape,
}

export default class Tabs extends PureComponent {
  constructor(props) {
    super(props)

    this.state = {
      selected: props.selected || props.defaultSelected,
    }
  }

  getChildContext() {
    return {
      aauiTabsAPI: {
        select: this.select,
        getSelected: this.getSelected,
      },
    }
  }

  componentDidMount() {
    Object.defineProperties(this, {
      selected: {
        get() {
          return this.state.selected
        },
        set(v) {
          if (this.props.selected === undefined) {
            this.setState({
              selected: v,
            })
          }
        },
      },
    })

    if (this.state.selected == null) {
      React.Children.forEach(this.props.children, child => {
        if (child.type === Tabs.Container) {
          this.setState({
            selected: child.props.name,
          })
        }
      })
    }
  }

  componentWillReceiveProps(nextProps) {
    const defaultSelected = nextProps.defaultSelected !== this.props.defaultSelected ?
      nextProps.defaultSelected : this.state.selected
    const newState = {
      selected: nextProps.selected !== this.props.selected ? nextProps.selected : defaultSelected,
    }

    if (newState.selected !== this.state.selected) this.setState(newState)
  }

  getSelected = () => this.state.selected

  select = name => {
    if (this.props.selected === undefined) {
      this.setState({
        selected: name,
      }, () => {
        if (this.props.onChange) this.props.onChange(name)
      })
    } else if (this.props.onChange) this.props.onChange(name)
  }

  render() {
    const { className, style, children } = this.props

    return (
      <div
        className={className || ''}
        style={style}
      >
        {children}
      </div>
    )
  }
}

Tabs.displayName = 'AAUITabs'
Tabs.propTypes = propTypes
Tabs.childContextTypes = childContextTypes

Tabs.Header = TabsHeader
Tabs.Title = getContext(childContextTypes)(TabsTitle)
Tabs.Container = getContext(childContextTypes)(TabsContainer)



