import React, { PureComponent, PropTypes } from 'react'
/* eslint import/no-extraneous-dependencies: 0 */
import ReactDOM from 'react-dom'
import 'active.css/less/components/date-picker.less'
import { cls } from './shared/utils'
import Input from './Input'

const MONTH_NAMES = [
  'January', 'February', 'March', 'April', 'May', 'June',
  'July', 'August', 'September', 'October', 'November', 'December',
]

const DAY_SPAN = 1000 * 60 * 60 * 24

function findOutDays(d) {
  const firstDayOfMonth = new Date(d.getFullYear(), d.getMonth(), 1)
  const startDate = new Date(firstDayOfMonth.getTime() - (DAY_SPAN * firstDayOfMonth.getDay()))

  function addDay(date, n) {
    const offset = date.getTimezoneOffset()
    const nextDay = new Date(date.getTime() + (n * DAY_SPAN))
    const nextDayOffset = nextDay.getTimezoneOffset()
    if (nextDayOffset !== offset) {
      nextDay.setTime(nextDay.getTime() + ((nextDayOffset - offset) * 60 * 1000))
    }
    return nextDay
  }

  const days = new Array(6)
  for (let i = 0; i < 6; i += 1) {
    days[i] = new Array(7)
    for (let j = 0; j < 7; j += 1) {
      days[i][j] = addDay(startDate, (i * 7) + j)
    }
  }
  return days
}

function formatDate(d) {
  if (d == null) return ''
  return `${d.getMonth() + 1}/${d.getDate()}/${d.getFullYear()}`
}

function isOtherMonth(x, y) {
  return x.getMonth() !== y.getMonth()
}

function isSameDay(x, y) {
  if (x === y) return true
  if (x == null || y == null) return false
  return x.getFullYear() === y.getFullYear()
    && x.getMonth() === y.getMonth()
    && x.getDate() === y.getDate()
}

const propTypes = {
  className: PropTypes.string,
  style: PropTypes.object,
  name: PropTypes.string,
  showIcon: PropTypes.bool,
  errored: PropTypes.bool,
  disabled: PropTypes.bool,
  onFocus: PropTypes.func,
  onBlur: PropTypes.func,
  transformDate: PropTypes.func,
  value: PropTypes.object,
  defaultValue: PropTypes.object,
  formatDate: PropTypes.func,
  today: PropTypes.object,
  getDateStatus: PropTypes.func,
  setDateClass: PropTypes.func,
  onClickDate: PropTypes.func,
  onChange: PropTypes.func,
}

export default class DatePicker extends PureComponent {

  constructor(props) {
    super(props)
    this.state = {
      isDisplayed: false,
      showOnTop: false,
      showOnLeft: false,
      value: props.value || props.defaultValue,
      calendarDate: props.value || props.defaultValue || new Date(),
    }
  }

  componentDidMount = () => {
    Object.defineProperties(this, {
      value: {
        get() {
          return this.state.value
        },
        set(v) {
          if (this.props.value === undefined) {
            this.setState({ value: v })
          }
        },
      },
    })
  }

  componentWillReceiveProps = nextProps => {
    /* eslint no-nested-ternary: 0 */
    const newState = {
      value: nextProps.value !== this.props.value
        ? nextProps.value
        : nextProps.defaultValue !== this.props.defaultValue
          ? this.state.value === undefined
            ? nextProps.defaultValue
            : this.state.value
          : this.state.value,
    }
    if (newState.value !== this.state.value) this.setState(newState)
  }

  componentDidUpdate = () => {
    const formatDateProps = this.props.formatDate
    const { value } = this.state

    if (formatDateProps) {
      this.input.value = formatDateProps(value)
    } else if (value) {
      this.input.value = formatDate(value)
    }
  }

  onClickDate = (d, dateStatusObj) => {
    let onClickDate = this.props.onClickDate
    onClickDate = typeof onClickDate === 'function' ? onClickDate : null

    if (onClickDate) {
      if (onClickDate(dateStatusObj)) {
        this.selectDate(d)
      }
      return false
    }

    this.selectDate(d)
    return false
  }

  setDateClass(c, calendarDate, today, dateStatusObj) {
    let setDateClass = this.props.setDateClass
    setDateClass = typeof setDateClass === 'function' ? setDateClass : null

    return cls`${isOtherMonth(c, calendarDate) ? 'date-picker--other-month' : ''}
      ${isSameDay(c, today) ? 'date-picker--today' : ''}
      ${isSameDay(c, this.state.value) ? 'date-picker--selected' : ''}
      ${setDateClass && setDateClass(dateStatusObj)}`
  }

  displayIfNotAlready = () => {
    if (document.activeElement === this.input.refs.input
      && !this.state.isDisplayed) {
      this.setState({ isDisplayed: true })
    }
  }

  preventStealingFocus = e => {
    e.preventDefault()
  }

  goPrevMonth = () => {
    const calendarDate = this.state.calendarDate
    const prevMonth = new Date(
      new Date(calendarDate.getFullYear(), calendarDate.getMonth(), 1).getTime() - DAY_SPAN,
    )
    this.setState({
      calendarDate: prevMonth,
    })
  }

  goNextMonth = () => {
    const calendarDate = this.state.calendarDate
    const nextMonth = new Date(
      new Date(calendarDate.getFullYear(), calendarDate.getMonth(), 1).getTime() + (DAY_SPAN * 31),
    )
    this.setState({
      calendarDate: nextMonth,
    })
  }

  selectDate = d => {
    const date = this.props.value !== undefined ? this.state.value : d

    this.setState({
      value: d && date,
      isDisplayed: false,
    }, () => {
      if (this.props.onChange) this.props.onChange(d)
    })
  }

  gainFocus = () => {
    /* eslint react/no-find-dom-node: 0 */
    const rect = ReactDOM.findDOMNode(this.input).getBoundingClientRect()
    const verDistance = document.documentElement.clientHeight -
      (rect.top + rect.height)
    const horDistance = document.documentElement.clientWidth -
      (rect.left + rect.width)

    this.setState({
      isDisplayed: true,
      showOnTop: verDistance < 216.75 && rect.top >= 216.75,
      showOnLeft: horDistance < 246 && rect.width <= 246,
      calendarDate: this.state.value || new Date(),
    })
  }

  loseFocus = () => {
    const input = this.input
    const value = this.state.value

    const transformDate = this.props.transformDate
    const d = new Date(transformDate ? transformDate(input.value) : input.value)

    if (isNaN(d.getTime())) {
      this.selectDate(null)
    } else if (value == null || d.getTime() !== value.getTime()) {
      this.selectDate(d)
    } else {
      this.setState({
        isDisplayed: false,
      })
    }
  }

  render = () => {
    const {
      className, style, name,
      showIcon, errored, disabled,
      onFocus, onBlur,
    } = this.props
    const { isDisplayed, showOnTop, showOnLeft } = this.state
    const calendarDate = this.state.calendarDate
    const calendarDateStr = `${MONTH_NAMES[calendarDate.getMonth()]} ${calendarDate.getFullYear()}`
    const days = findOutDays(calendarDate)
    const today = this.props.today || new Date()

    // set input element default value.
    let inputDefaultValue = this.props.value ? this.state.value : this.props.defaultValue
    inputDefaultValue = this.props.formatDate
      ? this.props.formatDate(inputDefaultValue)
      : formatDate(inputDefaultValue)
    const getDateStatus = this.props.getDateStatus || null

    return (
      <div
        className={cls`date-picker ${className || ''}`}
        style={style}
      >
        <Input
          ref={ref => { this.input = ref }}
          name={name}
          postIcon={showIcon ? 'icon-calendar' : undefined}
          errored={errored}
          disabled={disabled}
          defaultValue={inputDefaultValue}
          onFocus={
            onFocus
              ? e => {
                this.gainFocus()
                onFocus(e)
              }
              : this.gainFocus
          }
          onBlur={
            onBlur
              ? e => {
                this.loseFocus()
                onBlur(e)
              }
              : this.loseFocus
          }
          onClick={this.displayIfNotAlready}
        />
        <div
          className={cls`date-picker__calendar
                         ${!isDisplayed ? 'u-hidden' : ''}
                         ${showOnTop ? 'date-picker__calendar--show-on-top' : ''}
                         ${showOnLeft ? 'date-picker__calendar--show-on-left' : ''}`}
          onMouseDown={this.preventStealingFocus}
        >
          <header className='date-picker__header u-clearfix'>
            <span
              className='icon-chevron-left date-picker__left-arrow'
              title='Previous month'
              onClick={this.goPrevMonth}
            />
            <span
              className='icon-chevron-right date-picker__right-arrow'
              title='Next month'
              onClick={this.goNextMonth}
            />
            <h1 className='date-picker__calendar-title'>{calendarDateStr}</h1>
          </header>
          <table className='date-picker__table'>
            <thead>
              <tr>
                <th title='Sunday'>S</th>
                <th title='Monday'>M</th>
                <th title='Tuesday'>T</th>
                <th title='Wednesday'>W</th>
                <th title='Thursday'>T</th>
                <th title='Friday'>F</th>
                <th title='Saturday'>S</th>
              </tr>
            </thead>
            <tbody>
              {days.map((r, i) =>
                (
                  <tr key={i}>
                    {r.map((c, j) => {
                      const dateStatusObj = (getDateStatus && getDateStatus({
                        date: c,
                        calendarDate,
                        today,
                      })) || null

                      return (
                        <td
                          key={j}
                          className={this.setDateClass(c, calendarDate, today, dateStatusObj)}
                          onClick={() => this.onClickDate(c, dateStatusObj)}
                        >
                          {c.getDate()}
                        </td>
                      )
                    })}
                  </tr>
                ),
              )}
            </tbody>
          </table>
        </div>
      </div>
    )
  }

}

DatePicker.displayName = 'AAUIDatePicker'
DatePicker.propTypes = propTypes



