import createListenerCollection from './createListenerCollection'
import defaultValidationMessages from '../form/i18n/ValidationMsg.json'
import commonMessages from '../i18n/common/CommonMsg.json'
import { interpolate } from './utils'

const defaultValidationMsgKeys = {
  required: 'react-aaui.validation.required',
  limit: 'react-aaui.validation.limitation',
  max: 'react-aaui.validation.max',
  min: 'react-aaui.validation.min',
  invalid: 'react-aaui.validation.invalid',
}
const DEFAULT_LOCALE = 'en_US'

export default class L10n {
  constructor({ locale = DEFAULT_LOCALE, messages = {}, config = {} } = {}) {
    this._locale = locale
    this._messages = {
      ...defaultValidationMessages[this._locale], ...commonMessages[this._locale], ...messages,
    }
    this._config = config

    this.initDefaultMsg()
    this.listeners = createListenerCollection()
  }

  get locale() {
    return this._locale
  }

  set locale(value) {
    this._locale = value
    this.notifiy()
  }

  get messages() {
    return this._messages
  }

  set messages(value) {
    this._messages = value
    this.notifiy()
  }

  initDefaultMsg() {
    Object.keys(defaultValidationMsgKeys).forEach(k => {
      this[k] = defaultValidationMsgKeys[k]
    })
  }

  notifiy() {
    this.listeners.notify()
  }

  subscribe(listener) {
    if (typeof listener !== 'function') {
      throw new Error('Expected listener to be a function.')
    }

    return this.listeners.subscribe(listener)
  }

  formatMessage(id, values, defaultMessage = '') {
    const messages = this.messages

    return messages[id] ? interpolate(messages[id], values) : defaultMessage
  }
}



