import React from 'react'

import { parseRules } from './validation'
import L10nMessage from '../shared/L10nMessage'
import { cls } from '../shared/utils'
import { FormFieldPropTypes } from './types'

const propTypes = { ...FormFieldPropTypes }
const defaultProps = {
  rules: '',
}
/* eslint-disable jsx-a11y/label-has-for */
const VFormFieldLayout = ({ label, errMsg, required, children, rules }) => {
  const parsedRules = parseRules(rules)
  const l10nMessageValues = parsedRules.reduce((r, rule) => ({
    ...r,
    [rule.name]: rule.param,
  }), {})

  return (
    <div
      className={cls`form__group
        ${errMsg ? 'form__group--error' : ''}`}
    >
      <label className={cls`form__label ${required ? 'form__label--require' : ''}`}><L10nMessage id={label} /></label>
      <div className='form__control'>
        {children}
      </div>
      {
        errMsg &&
          <div className='form__validate'>
            <i className='icon-close-circle' /><L10nMessage id={errMsg} values={l10nMessageValues} />
          </div>
      }
    </div>
  )
}

VFormFieldLayout.propTypes = propTypes
VFormFieldLayout.defaultProps = defaultProps

export default VFormFieldLayout



