import React, { createElement, PureComponent } from 'react'

import getDisplayName from '../shared/getDisplayName'
import { FormFieldPropTypes } from './types'

const NON_LAYOUT_FIELD = 'NonLayoutField'
const formFields = []
const propTypes = { ...FormFieldPropTypes }

export const registerFormField = Field => {
  if (formFields.indexOf(Field) < 0) {
    formFields.push(Field)
  }
}
export const isRegisteredField = type => formFields.indexOf(type) >= 0

const createField = FieldLayout => {
  class Field extends PureComponent {
    render() {
      const { component, errMsg, label, ...rest } = this.props
      const rules = this.props.rules || ''
      const required = this.props.required || rules.indexOf('required') !== -1
      let element

      if (typeof component === 'string') {
        element = createElement(component, { label, ...rest })
      } else {
        element = createElement(component, { errMsg, label, ...rest })
      }

      if (FieldLayout) {
        return (
          <FieldLayout label={label} errMsg={errMsg} required={required} {...rest}>
            {element}
          </FieldLayout>
        )
      }

      return element
    }
  }

  Field.displayName = `FormField(${getDisplayName(FieldLayout || NON_LAYOUT_FIELD)})`
  Field.propTypes = propTypes

  registerFormField(Field)

  return Field
}

export default createField



