import React, { PropTypes } from 'react'
import 'active.css/less/components/pill.less'

import { cls } from './shared/utils'


const { string, object, any, oneOf } = PropTypes

const propTypes = {
  className: string,
  style: object,
  size: oneOf(['xs', 'sm', 'lg', 'xl']),
  children: any,
}

const Pills = ({ className, style, size, children }) => (
  <div
    className={cls`btn-group
      ${size ? `btn-group-${size}` : ''}
      ${className || ''}`}
    style={style}
  >
    {children}
  </div>
)

Pills.displayName = 'AAUIPills'
Pills.propTypes = propTypes

export default Pills



