import { PropTypes } from 'react'

import Store from './store'

const { bool, string, object, func, shape, instanceOf, any } = PropTypes

export const FormPropTypes = {
  onSubmit: func,
  onFail: func,
  onChange: func,
  defaultValues: object,
}

export const FormFieldAPIPropTypes = {
  api: shape({
    setValue: func,
    setError: func,
    onValidate: func,
  }),
}

export const FormFieldPropTypes = {
  rules: string,
  value: any,
  parser: func,
  formatter: func,
  validator: func,
  errMsg: any,
  fields: object,
  required: bool,
}

export const FormStorePropTypes = {
  aauiFormStore: instanceOf(Store),
}



