import React, { PureComponent, PropTypes } from 'react'
import 'active.css/less/components/popover.less'

import { cls } from './shared/utils'

const { string, object, func, array, element, oneOfType } = PropTypes

const propTypes = {
  className: string,
  direction: string,
  style: object,
  children: oneOfType([array, object, func, element]),
}

const defaultProps = {
  direction: 'e',
}

export default class Popover extends PureComponent {
  render = () => {
    const { className, style, direction, children } = this.props
    return (
      <div className={cls`popover popover--${direction} ${className || ''}`} style={style}>
        {children}
      </div>
    )
  }
}

Popover.displayName = 'AAUIPopover'
Popover.propTypes = propTypes
Popover.defaultProps = defaultProps



