import React, { PureComponent, PropTypes } from 'react'
/* eslint import/extensions: 0, import/no-unresolved: 0 */
/* eslint import/no-extraneous-dependencies: 0, import/no-webpack-loader-syntax: 0 */
import TweenLite from 'imports?define=>false!gsap/src/uncompressed/TweenLite'
import 'imports?define=>false!gsap/src/uncompressed/plugins/CSSPlugin'

import { cls } from './shared/utils'

import './Carousel.less'

const { string, object, func, array, element, oneOfType, number, any } = PropTypes

const carouselPropTypes = {
  className: string,
  style: object,
  children: oneOfType([array, object, func, element]),
  selected: number,
  defaultSelected: number,
  onChange: func,
}

const defaultCarouselPropTypes = {
  shown: false,
}

class Carousel extends PureComponent {

  constructor(props) {
    super(props)
    this.state = {
      selected: this.props.selected != null
        ? this.props.selected
        : this.props.defaultSelected || 0,
    }
  }

  componentDidMount() {
    Object.defineProperties(this, {
      selected: {
        get() {
          return this.state.selected
        },
        set(v) {
          if (this.props.selected === undefined) {
            this.setState({ selected: v })
          }
        },
      },
    })

    const slidesCount = React.Children.count(this.props.children)
    TweenLite.set(this.slides, { x: `-${(100 / slidesCount) * this.state.selected}%` })
  }

  /* eslint no-nested-ternary:  */
  componentWillReceiveProps(nextProps) {
    const newState = {
      selected: nextProps.selected !== this.props.selected
        ? nextProps.selected
        : nextProps.defaultSelected !== this.props.defaultSelected
          ? this.state.selected === undefined
            ? nextProps.defaultSelected
            : this.state.selected
          : this.state.selected,
    }
    if (newState.selected !== this.state.selected) this.setState(newState)
  }

  componentDidUpdate(_, { selected: prevSelected }) {
    const selected = this.state.selected
    if (prevSelected !== selected) {
      const slidesCount = React.Children.count(this.props.children)
      TweenLite.to(this.slides, 0.3, { x: `-${(100 / slidesCount) * selected}%` })
    }
  }

  select = ind => () => {
    if (this.props.selected === undefined) {
      this.setState({ selected: ind }, () => {
        if (this.props.onChange) this.props.onChange(ind)
      })
    } else if (this.props.onChange) this.props.onChange(ind)
  }

  render() {
    const { className, style, children } = this.props
    const { selected } = this.state
    const slidesCount = React.Children.count(children)
    const slides = React.Children.map(children, slide => React.cloneElement(slide, { slidesCount }))

    return (
      <div
        className={`aaui-carousel ${className || ''}`}
        style={style}
      >
        <div
          ref={ref => {
            this.slides = ref
          }}
          className="aaui-carousel-slides"
          style={{ width: `${100 * slidesCount}%` }}
        >
          {slides}
        </div>
        <ul className="aaui-carousel-arrows">
          <li>
            <span
              className={cls`aaui-icon-left-arrow
                              aaui-carousel-left-arrow
                              ${selected === 0 ? 'aaui-hidden' : ''}`}
              onClick={this.select(selected - 1)}
            />
          </li>
          <li>
            <span
              className={cls`aaui-icon-right-arrow
                              aaui-carousel-right-arrow
                              ${selected === slidesCount - 1 ? 'aaui-hidden' : ''}`}
              onClick={this.select(selected + 1)}
            />
          </li>
        </ul>
        <ul className="aaui-carousel-dot">
          {React.Children.map(children, (slide, i) => (
            <li
              key={i}
              className={i === selected ? 'aaui-carousel-active-dot' : undefined}
              onClick={this.select(i)}
            />
            ),
          )}
        </ul>
      </div>
    )
  }

}

Carousel.displayName = 'AAUICarousel'
Carousel.propTypes = carouselPropTypes
Carousel.defaultProps = defaultCarouselPropTypes

const slidePropTypes = {
  className: string,
  style: object,
  children: any,
  slidesCount: number,
}

const defaultSlidePropTypes = {
  shown: false,
}

/* eslint react/no-multi-comp: 0 */
class Slide extends PureComponent {
  render() {
    const { className, style, children, slidesCount } = this.props

    return (
      <div
        className={`aaui-carousel-slide ${className || ''}`}
        style={Object.assign({ width: `${100 / slidesCount}%` }, style)}
      >
        {children}
      </div>
    )
  }
}

Slide.displayName = 'AAUICarouselSlide'
Slide.propTypes = slidePropTypes
Slide.defaultProps = defaultSlidePropTypes

Carousel.Slide = Slide

export default Carousel



