import React, { PureComponent, PropTypes } from 'react'
import 'active.css/less/components/dropdown.less'

import { cls, debounce } from './shared/utils'
import * as da from './shared/data-access'


const { string, bool, func, object, any } = PropTypes
const propTypes = {
  isMoreButton: bool,
  placeholder: string,
  className: string,
  value: string,
  disabled: bool,
  defaultValue: string,
  maxHeight: string,
  data: any,
  filter: bool,
  onChange: func,
  filterPlaceholder: string,
  style: object,
}

const defaultProps = {
  placeholder: 'Select one...',
  filterPlaceholder: 'Filter...',
}

export default class Dropdown extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      isExpanded: false,
      value: this.props.value || this.props.defaultValue,
      dataView: this.props.data,
      activeItemIndex: -1,
    }
  }

  componentWillMount = () => {
    this.keyboardValue = ''
    this.clearKeyboardValue = debounce(() => {
      this.keyboardValue = ''
    }, 600)
  }

  componentDidMount = () => {
    Object.defineProperties(this, {
      value: {
        get() {
          return this.state.value
        },
        set(v) {
          if (this.props.value === undefined) {
            this.setState({ value: v })
          }
        },
      },
    })
  }

  componentWillReceiveProps = nextProps => {
    const newState = {}
    if (nextProps.value !== this.props.value) {
      newState.value = nextProps.value
    } else if (nextProps.defaultValue !== this.props.defaultValue) {
      newState.value = this.state.value === undefined
        ? nextProps.defaultValue
        : this.state.value
    }
    if (nextProps.data !== this.props.data) {
      newState.dataView = nextProps.data
    }
    if (Object.keys(newState).length > 0) this.setState(newState)
  }

  componentDidUpdate = () => {
    if (this.state.isExpanded) {
      if (this.props.filter) {
        this.filterInput.focus()
      } else {
        this.itemMenu.focus()
      }

      const itemMenu = this.itemMenu
      const activeItem = itemMenu.querySelector('.selected')
      if (activeItem) {
        const itemMenuHeight = itemMenu.offsetHeight
        const itemMenuScrollTop = itemMenu.scrollTop
        const activeItemTop = activeItem.offsetTop
        const activeItemHeight = activeItem.offsetHeight
        /* eslint no-mixed-operators: 0 */
        if (activeItemTop + activeItemHeight - itemMenuHeight - itemMenuScrollTop > 0) {
          itemMenu.scrollTop = activeItemTop + activeItemHeight - itemMenuHeight
        } else if (activeItemTop < itemMenuScrollTop) {
          itemMenu.scrollTop = activeItemTop
        }
      }
    }
  }

  getIndex = str => {
    const dataArray = this.props.data.toJS ? this.props.data.toJS() : this.props.data
    let index = 0
    let value = ''
    const length = da.count(dataArray)
    let isFound = false
    let keyWords = ''
    let i = 0
    if (this.state.keyWords && str.toUpperCase().indexOf(this.state.keyWords) === 0) {
      i = this.state.activeItemIndex + 1
    }

    for (; i < length; i += 1) {
      value = da.get(dataArray[i], 'text')
      if (value.toUpperCase().indexOf(str) === 0) {
        index = i
        isFound = true
        keyWords = str
        break
      }
    }

    if (this.state.keyWords && str.toUpperCase().indexOf(this.state.keyWords) === 0) {
      if (!isFound) {
        for (i = 0; i < length; i += 1) {
          value = da.get(dataArray[i], 'text')
          if (value.toUpperCase().indexOf(str) === 0) {
            index = i
            isFound = true
            keyWords = str
            break
          }
        }
      }
    }

    return { index, isFound, keyWords }
  }

  tryCollapse = () => {
    if (this.state.isExpanded) {
      this._timer = setTimeout(() => {
        this.setState({
          isExpanded: false,
          // activeItemIndex: -1
        })
      }, 100)
    }
  }

  cancelCollapseTimeout = () => {
    clearTimeout(this._timer)
  }

  giveFocus = e => {
    if (e.target === this.itemMenu) {
      this.itemMenu.focus()
    }
  }

  navigateByKeys = e => {
    if (e.keyCode === 9) {
      return
    } //  focus next component if there are many input, dropdown
    // or others component on the screen when pressing tab.
    let isExpanded = false
    e.preventDefault()
    /* eslint no-case-declarations: 0 */
    switch (e.keyCode) {
      case 38: // up (Previous item)
        this.setState({
          activeItemIndex: this.state.activeItemIndex - 1 >= 0
            ? this.state.activeItemIndex - 1
            : da.count(this.state.dataView) - 1,
          isExpanded: true,
        })
        break
      case 40: // down (Next item)
        this.setState({
          activeItemIndex: this.state.activeItemIndex + 1 < da.count(this.state.dataView)
            ? this.state.activeItemIndex + 1
            : 0,
          isExpanded: true,
        })
        break
      case 13: // enter (Select the active item)
        const { dataView, activeItemIndex } = this.state
        let value = da.get(dataView, activeItemIndex)
        value = da.get(value, 'value')
        this.select(value)
        this.dropdownButton.focus()
        break
      case 27: // escape (Hide dropdown menu)
        this.setState({
          isExpanded: false,
          activeItemIndex: this.state.activeItemIndex,
        })
        this.dropdownButton.focus()
        break
      case 32: // blank (Show dropdown menu)
        isExpanded = this.state.isExpanded
        if (isExpanded) {
          return /* eslint no-useless-return: 0 */
        } else { /* eslint no-else-return: 0 */
          this.setState({
            isExpanded: true,
            activeItemIndex: this.state.activeItemIndex,
          })
        }
        break
      default: // Filter dropdown list by the key word
        if (!this.props.filter) {
          const charStr = String.fromCharCode(e.keyCode)
          this.keyboardValue = this.keyboardValue + charStr
          this.filterFromKeyboard()
        }
    }
  }

  applyFilter = e => {
    this.setState({
      dataView: this.filterData(e.target.value, this.props.data),
    })
  }

  handleKeys = e => {
    // Delegate up, down, and escape to itemMenu.
    if ([38, 40, 27].indexOf(e.keyCode) > -1) return
    // Delegate enter to itemMenu if up/down selections are already made.
    if (e.keyCode === 13 && this.state.activeItemIndex > -1) return
    e.stopPropagation()
  }

  select = value => {
    const val = value

    this.setState({
      value: val,
      isExpanded: false,
      activeItemIndex: -1,
    }, () => {
      if (this.props.onChange) this.props.onChange({ value })
    })
  }

  findItemByValue = value => {
    /* eslint no-restricted-syntax: 0 */
    for (const item of this.props.data) {
      if (da.get(item, 'value') === value) {
        return item
      }
    }
    return undefined
  }

  findTextByValue = value => {
    const item = this.findItemByValue(value)
    if (item) {
      return da.get(item, 'text')
    } else {
      return undefined
    }
  }

  filterData = (key, dataset) => {
    const k = key.trim().toLowerCase()
    const klen = k.length

    function matcher(item) {
      const t = da.get(item, 'text').trim().toLowerCase()
      let i = 0
      let kc = k.charAt(i)
      for (const tc of t) {
        if (tc === kc) {
          i += 1
          if (i >= klen) {
            return true
          }
          kc = k.charAt(i)
        }
      }
      return false
    }

    return (!k ? dataset : dataset.filter(matcher))
  }

  clearKeyboard = e => {
    e.persist()
    this.clearKeyboardValue(e)
  }

  filterFromKeyboard = () => {
    const result = this.getIndex(this.keyboardValue)
    if (result.isFound) {
      this.setState({
        activeItemIndex: result.index,
        keyWords: result.keyWords,
      })
    }
  }

  toggleMenu = () => {
    clearTimeout(this._timer)

    this.setState({
      isExpanded: !this.state.isExpanded,
    })
  }

  handleInputOnclick = value => () => {
    this.select(value)
  }

  render = () => {
    const {
      className, style,
      disabled, placeholder,
      maxHeight,
      filter, filterPlaceholder,
      isMoreButton,
    } = this.props
    const { isExpanded, value, dataView, activeItemIndex } = this.state
    /* eslint jsx-a11y/role-has-required-aria-props: 0, jsx-a11y/aria-proptypes: 0 */
    /* eslint jsx-a11y/href-no-hash: 0, jsx-a11y/tabindex-no-positive: 0 */
    return (
      <div
        className={cls`dropdown ${className || ''}`}
        style={style}
        onKeyUp={disabled ? undefined : this.clearKeyboard}
        onKeyDown={disabled ? undefined : this.navigateByKeys}
      >
        <button
          type='button'
          ref={ref => { this.dropdownButton = ref }}
          role='button'
          aria-label={this.findTextByValue(value)}
          /* eslint no-nested-ternary: 0 */
          className={cls`dropdown__button ${isMoreButton ? (isExpanded ? 'show' : '') : isExpanded ? 'expand' : 'collapse'} ${disabled ? 'disabled' : ''}`}
          onMouseDown={e => e.preventDefault()}
          onClick={disabled ? undefined : this.toggleMenu}
        >
          <span className='dropdown__button-text'>{this.findTextByValue(value) || placeholder}</span>
          {isMoreButton ?
            <span className={'icon-triangle-down'} /> :
            <span className={cls`${isExpanded ? 'icon-chevron-up' : 'icon-chevron-down'}`} />
          }
        </button>
        <div role='combobox'>
          <ul
            className={cls`dropdown__menu ${isExpanded && !disabled ? 'show' : ''}`}
            aria-expanded={isExpanded && !disabled}
            ref={ref => { this.itemMenu = ref }}
            role='listbox'
            tabIndex='1'
            style={{ maxHeight }}
            onMouseDown={this.giveFocus}
            onFocus={this.cancelCollapseTimeout}
            onBlur={this.tryCollapse}
          >
            {filter
              ? (
                <li>
                  <div className='dropdown__menu__search-box'>
                    <i className='icon-search' />
                    <input
                      type='text'
                      className='input'
                      ref={ref => { this.filterInput = ref }}
                      role='textbox'
                      aria-label='Search'
                      autoComplete='off'
                      placeholder={filterPlaceholder}
                      onKeyDown={this.handleKeys}
                      onChange={this.applyFilter}
                    />
                  </div>
                </li>
              )
              : undefined}
            {filter
              ? (
                <li className='dropdown__menu-divider' role='separator' />
              )
              : undefined}
            {dataView.map((item, i) => (
              <li
                key={da.get(item, 'value')}
                role='option'
                className={i === activeItemIndex ? 'selected' : undefined}
                aria-selected={i === activeItemIndex ? true : undefined}
                onClick={this.handleInputOnclick(da.get(item, 'value'))}
              >
                <a>{da.get(item, 'text')}</a>
              </li>
              ),
            )}
          </ul>
        </div>
      </div>
    )
  }
}

Dropdown.displayName = 'AAUIDropdown'
Dropdown.propTypes = propTypes
Dropdown.defaultProps = defaultProps



