import React, { PureComponent, PropTypes } from 'react'

import 'active.css/less/icon.less'
import 'active.css/less/components/input-group.less'

import { cls, debounce } from './shared/utils'

const { string, number, func, oneOf } = PropTypes
const propTypes = {
  placeholder: string,
  searchBtnText: string,
  size: oneOf(['', 'lg']),
  onChange: func,
  delay: number,
}
const defaultProps = {
  placeholder: 'Enter your search here...',
  searchBtnText: 'Search',
  size: '',
  delay: 500,
}

export default class SearchInput extends PureComponent {
  constructor(props) {
    super(props)

    this.state = {
      value: '',
    }
    this.debouncedOnChange = debounce(this.debouncedOnChange, props.delay)
  }

  debouncedOnChange = value => {
    const { onChange } = this.props

    if (typeof onChange === 'function') {
      onChange(value.trim())
    }
  }

  handleChange = e => {
    const value = e.target.value

    this.setState({
      value,
    }, () => {
      this.debouncedOnChange(value)
    })
  }

  handleCloseIconClick = () => {
    this.setState({
      value: '',
    })
  }

  render() {
    /* eslint-disable no-unused-vars */
    const { placeholder, searchBtnText, size, delay, ...rest } = this.props
    const { value } = this.state
    const hasFeedback = !!value

    return (
      <div
        className={cls`
          search-box
          ${size ? `search-box--${size}` : ''}
        `}
      >
        <div
          className={cls`
            search-box__input-addon-group
            ${hasFeedback ? 'search-box__input-addon-group--has-feedback' : ''}
          `}
        >
          <i className='icon-search' />
          <input
            {...rest}
            className='input'
            type='search'
            value={value}
            placeholder={placeholder}
            onChange={this.handleChange}
          />
          {
            hasFeedback &&
              <span className='icon-close input-feedback active' onClick={this.handleCloseIconClick} />
          }
        </div>
        <button type='button' className='btn btn-primary'>{searchBtnText}</button>
      </div>
    )
  }
}

SearchInput.displayName = 'SearchInput'
SearchInput.propTypes = propTypes
SearchInput.defaultProps = defaultProps



