import React, { PureComponent, PropTypes } from 'react'
import 'active.css/less/components/button.less'

import { cls } from './shared/utils'


const { bool, string, any } = PropTypes

const propTypes = {
  noSubmit: bool,
  type: string.isRequired,
  size: string,
  className: string,
  children: any,
}

const defaultProps = {
  noSubmit: false,
  type: 'secondary',
}

export default class Button extends PureComponent {
  render() {
    const { noSubmit, type, size, children, className, ...rest } = this.props

    return (
      <button
        {...rest}
        type={noSubmit ? 'button' : 'submit'}
        className={cls`btn
          btn-${type}
          ${size ? `btn--${size}` : ''}
          ${className || ''}`}
      >
        {children}
      </button>
    )
  }
}

Button.displayName = 'AAUIButton'
Button.propTypes = propTypes
Button.defaultProps = defaultProps



