import 'active.css/less/components/progress-steps.less'

import React, { PureComponent, PropTypes } from 'react'
import { cls } from './shared/utils'
import * as da from './shared/data-access'


const { string, object, array } = PropTypes

const propTypes = {
  className: string,
  size: string,
  steps: array,
  style: object,
}

export default class ProgressSteps extends PureComponent {
  static displayName = 'AAUIProgressSteps'

  render() {
    const { className, style, size, steps } = this.props
    return (
      <ul className={cls`progress-steps ${size ? `progress-steps--${size}` : ''} ${className || ''}`} style={style}>
        {
          steps.map(step =>
            <li key={da.get(step, 'text')} className={`progress-steps__step ${da.get(step, 'status')}`}>
              <span className='progress-steps__step-text'>{da.get(step, 'text')}</span>
            </li>,
          )
        }
      </ul>
    )
  }
}

ProgressSteps.displayName = 'AAUIProgressSteps'
ProgressSteps.propTypes = propTypes



