import React, { PropTypes } from 'react'

import { tabsAPIShape } from './shared/types'


const { string, object, any } = PropTypes

const propTypes = {
  className: string,
  style: object,
  name: string,
  children: any,
  aauiTabsAPI: tabsAPIShape,
}

const TabsTitle = ({ name, style, className, children, aauiTabsAPI: { select, getSelected } }) => (
  <li>
    <a
      className={`${name === getSelected() ? 'active' : ''} ${className || ''}`}
      style={style}
      onClick={() => select(name)}
    >
      {children}
    </a>
  </li>
)

TabsTitle.displayName = 'AAUITabsTitle'
TabsTitle.propTypes = propTypes

export default TabsTitle



