import React, { PureComponent, PropTypes } from 'react'
import 'active.css/less/components/more-button.less'

import { cls } from './shared/utils'
import * as da from './shared/data-access'
import Dropdown from './Dropdown'

const { string, object, bool } = PropTypes

const propTypes = {
  className: string,
  title: string,
  data: object,
  style: object,
  filter: bool,
  filterPlaceholder: string,
  disabled: bool,
}

const defaultProps = {
  title: 'More',
}

export default class MoreButton extends PureComponent {

  invokeAction = ({ value: k }) => {
    da.get(this.props.data, k)()
  }

  render = () => {
    const { className, style, title, filter, filterPlaceholder, disabled } = this.props
    const data = da.keys(this.props.data).map(k => ({ text: k, value: k }))

    return (
      <Dropdown
        className={cls`more-button ${className || ''}`}
        style={style}
        placeholder={title}
        filter={filter}
        filterPlaceholder={filterPlaceholder}
        data={data}
        value=''
        disabled={disabled}
        isMoreButton
        onChange={this.invokeAction}
      />
    )
  }
}

MoreButton.displayName = 'AAUIMoreButton'
MoreButton.propTypes = propTypes
MoreButton.defaultProps = defaultProps



