import { PropTypes } from 'react'

import L10n from './L10n'

const { func, shape, instanceOf } = PropTypes

export const tabsAPIShape = shape({
  select: func,
  getSelected: func,
})

export const aauiL10nPropTypes = {
  aauiL10n: instanceOf(L10n),
}



